import { useState } from 'react';
import './App.css';
import { useDispatch } from 'react-redux';
import {addTodo,deleteTodo,removeTodo} from  './actions/index.js';
import { useSelector } from 'react-redux';

function App() {


  let date = new Date()

  let day=date.getDay()
let weekDay = new Array(7)
  weekDay=["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
 
 const dispatch = useDispatch()

  const [input, setInput] = useState("")
 const list = useSelector((state) => state.reducerTodo.list)
 
  
  return (
    <div className="App">
      <div className="container">
     <div className="mainHeading">
        <h1>ToDo List</h1>
      </div>
      <div className="subHeading">
        <br />
        <h2>Whoop, it's {weekDay[day]} üåù ‚òï </h2>
      </div>
      <div className="input">
        <input type="text" placeholder="üñäÔ∏è Add item..." value={input} onChange={(e)=>setInput(e.target.value)}/>
     {input &&   <i className="fas fa-plus" onClick={()=>dispatch(addTodo(input),setInput(""))}></i>}
      </div>
    {
      list.map((obj)=>{
        return(
          <div className="inputtwo" key={obj.id}>
          <input type="text" id='secondinput' value={obj.data} disabled />
          <i className="fas fa-trash" onClick={()=>dispatch(deleteTodo(obj.id))} ></i>
        </div>

        )
      })
    }
     <div className="removebtn">
      <button className="remove" onClick={()=>dispatch(removeTodo())}>Remove all</button>
    </div>
    
      </div>
      </div>
   
  );
}

export default App;
